//глобальные переменные
@tablet-width:768px;
@desktop-width:1024px;
@big-desktop-width:1920px;

//основной цвет текста
@color-text:rgba(51, 51, 51, 1);

//паттерны
.list(){
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.button(){
  display: block;
  cursor: pointer;
  padding: 0;
  border: none;
  background-color: transparent;
}

//паттерн видимости
.visual-hidden{
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}