.visual-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
/*regular-400*/
@font-face {
  font-family: Open Sans;
  src: url(../fonts/OpenSans-Regular.woff2) format("woff2"), url(../fonts/OpenSans-Regular.woff) format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
/*semi bold-600*/
@font-face {
  font-family: Open Sans;
  src: url(../fonts/OpenSans-SemiBold.woff2) format("woff2"), url(../fonts/OpenSans-SemiBold.woff) format("woff");
  font-style: normal;
  font-weight: 600;
  font-display: swap;
}
/*bold-700*/
@font-face {
