  font-weight: 700;
  font-display: swap;
}
body {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  padding: 0;
  min-width: 320px;
}
.page-header {
  color: #ffffff;
}
.navigation {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 12px 14px 11.33px 15px;
  background-color: rgba(0, 0, 0);
  min-width: 320px;
  box-sizing: border-box;
}
@media (min-width: 768px) {
  .navigation {
    padding: 29px 50px;
  }
}
@media (min-width: 1024px) {
  .navigation {
    justify-content: space-between;
    padding: 30px 50px 30px 50px;
  }
}
@media (min-width: 1920px) {
  .navigation {
    padding: 30px 158px 30px 160px;
    flex-wrap: wrap;
  }
}
@media (max-width: 1023px) {
  .navigation_open {
    min-height: 100vh;
    min-width: 320px;
    padding: 16px 0 26px 16px;
    flex-direction: column;
    align-items: flex-start;
    background-color: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
  .navigation_open .navigation__burger {
    width: 15px;
    height: 15px;
    margin-right: initial;
    margin-bottom: 16px;
  }
  .navigation_open .navigation__burger::before {
    width: 110%;
    top: 43%;
    transform: rotate(45deg);
    box-shadow: initial;
  }
  .navigation_open .navigation__burger::after {
    width: 110%;
    top: 43%;
    transform: rotate(-45deg);
  }
  .navigation_open .navigation__logo {
    display: none;
  }
  .navigation_open .navigation__list {
    display: block;
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 263px;
  }
  .navigation_open .button-enter {
    display: block;
  }
  .navigation_open .navigation__search {
    display: none;
  }
  .navigation_open .navigation__form {
    display: none;
  }
}
@media (max-width: 1023px) and (min-width: 768px) {
  .navigation_open {
    padding: 38px 0 50px 49px;
  }
}
@media (max-width: 1023px) and (min-width: 768px) {
  .navigation_open .navigation__burger {
    width: 29px;
    height: 29px;
    margin-bottom: 30px;
  }
}
@media (max-width: 1023px) and (min-width: 768px) {
  .navigation_open .navigation__list {
    margin-bottom: 73px;
    padding-bottom: 72px;
