}
.navigation__burger {
  display: block;
  cursor: pointer;
  padding: 0;
  border: none;
  background-color: transparent;
  width: 25px;
  height: 18px;
  position: relative;
  margin-right: auto;
}
.navigation__burger::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  top: 0;
  left: 0;
  box-shadow: 0 8px 0 0 #ffffff;
}
.navigation__burger::after {
  position: absolute;
  content: '';
  width: 50%;
  height: 2px;
  background-color: #ffffff;
  bottom: 0;
  left: 0;
}
@media (min-width: 768px) {
  .navigation__burger {
    width: 36px;
    height: 28px;
  }
  .navigation__burger::before {
    box-shadow: 0 12px 0 0 #ffffff;
  }
}
