body{
  font-family: "Open Sans",sans-serif;
  margin: 0;
  padding: 0;
  min-width: 320px;
}
//header
.page-header {
  color:rgba(255, 255, 255, 1);
}
.navigation {
  position: absolute;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  padding: 12px 14px 11.33px 15px;
  background-color: rgba(0, 0, 0);
  min-width: 320px;
  box-sizing: border-box;
  @media(min-width:@tablet-width){
    padding: 29px 50px;
  }
  @media(min-width:@desktop-width){
    justify-content: space-between;
    padding: 30px 50px 30px 50px;
  }
  @media(min-width:@big-desktop-width){
    padding: 30px 158px 30px 160px;
    flex-wrap: wrap;
  }
}
@media(max-width:1023px){
  .navigation_open{
    min-height: 100vh;
    min-width: 320px;
    padding: 16px 0 26px 16px;
    flex-direction: column;
    align-items: flex-start;
    background-color: rgba(0, 0, 0,0.9);
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    @media(min-width:@tablet-width){
      padding: 38px 0 50px 49px;
    }
  }
  .navigation_open .navigation__burger{
    width: 15px;
    height: 15px;
    margin-right: initial;
    margin-bottom: 16px;
    &::before{
      width: 110%;
      top: 43%;
      transform: rotate(45deg);
      box-shadow: initial;
    }
    &::after{
      width: 110%;
      top: 43%;
      transform: rotate(-45deg);
    }
    @media(min-width:@tablet-width){
      width: 29px;
      height: 29px;
      margin-bottom:30px;
    }
  }
  .navigation_open .navigation__logo{
    display: none
  }
  .navigation_open .navigation__list{
    display: block;
    width: 100%;
    box-sizing: border-box;
    margin-bottom: 263px;
    @media(min-width:@tablet-width){
      margin-bottom:73px;
      padding-bottom: 72px;
    }
  }
  .navigation_open .navigation__link{
  }
  .navigation_open .button-enter{
    display: block;
  }
  .navigation_open .navigation__search{
    display: none;
  }
  .navigation_open .navigation__form{
    display: none
  }
}
.navigation__burger {
  .button();
  width: 25px;
  height: 18px;
  position: relative;
  margin-right: auto;
  &::before{
    position: absolute;
    content: '';
    width: 100%;
    height: 2px;
    background-color: rgba(255, 255, 255, 1);
    top: 0;
    left: 0;
    box-shadow: 0 8px 0 0 rgba(255, 255, 255, 1);
  }
  &::after{
    position: absolute;
    content: '';
    width: 50%;
    height: 2px;
    background-color: rgba(255, 255, 255, 1);
    bottom: 0;
    left: 0;
  }
  @media(min-width:@tablet-width){
    width: 36px;
    height: 28px;
    &::before{
      box-shadow: 0 12px 0 0 rgba(255, 255, 255, 1);
    }
  }
  @media(min-width:@desktop-width){
    display: none;
  }
}
.navigation__logo {
 margin-right: auto;
 @media(min-width:@desktop-width){
  margin-right:0;
 }
 @media(min-width:@big-desktop-width){
  margin-right:179px;
 }
}
.navigation__logo-image {
  display: block;
  width: 96.8px;
  height: 21.67px;
  @media(min-width:@tablet-width){
    width: 188px;
    height: 42px;
  }
  @media(min-width:@desktop-width){
    width: 173px;
    height: 40px;
  }
}
//список меню
.navigation__list {
  .list();
  @media(min-width:@desktop-width){
    display: flex;
    flex-wrap: wrap;
    margin-right:0;
    max-width: 900px;
  }
  @media(min-width:@big-desktop-width){
    margin-right: auto;
  }
}
.navigation__item {
  @media(min-width:@desktop-width){
    &:not(:last-child){
      margin-right: 30px;
    }
  }
  @media(min-width:@big-desktop-width){
    &:not(:last-child){
      margin-right: 62px;
    }
  }
}
.navigation__link {
  display: block;
  color: rgba(255, 255, 255, 1);
  text-decoration: none;
  font-weight: 600;
  font-size: 16px;
  line-height: 32px;
  padding: 2.7px 0;
  @media(min-width:@tablet-width){
    font-size:24px;
    line-height: 64px;
  }
  @media(min-width:@desktop-width){
    font-size: 18px;
    padding: 5px 0;
    line-height: normal;
  }
}
//кнопка входа/авторизации
.button-enter {
  .button();
  display: none;
  width: 115px;
  color: rgba(255, 255, 255, 1);
  border-radius: 100px;
  font-family: "Open Sans",sans-serif;
  font-weight: 600;
  font-size: 12px;
  padding: 9.5px 0;
  border:2px solid #9D5CD0;
  @media(min-width:@tablet-width){
    width: 257px;
    font-size: 18px;
    padding: 16px 0;
  }
  @media(min-width:@desktop-width){
    font-weight: 400;
    display: block;
    padding: 5px 0;
    border: none;
    width:initial;
  }
}
.button-enter__icon {
  width: 9px;
  height: 9px;
  @media(min-width:@tablet-width){
    width: 13px;
    height: 13px;
  }
  @media(min-width:@desktop-width){
    margin-right: 7px;
  }
}
//кнопка поиска
.navigation__search {
  .button();
  @media(min-width:@desktop-width){
    display: none;
  }
}
.navigation__search-icon {
  display: block;
  width: 18px;
  height: 18px;
  @media(min-width:@tablet-width){
    width: 27px;
    height: 27px;
  }
  @media(min-width:@desktop-width){
    width: 29.45px;
    height: 28.82px;
  }
}

//форма поиска
.navigation__form {
  display: none;
  background-color: black;
  position: absolute;
  left: 0;
  right: 0;
  padding: 13px 15px 14px 15px;
  @media(min-width:@tablet-width){
    padding:  33px 50px 35px 50px;
    align-items: flex-end;
  }
  @media(min-width:@desktop-width){
    padding:10px 0;
    left: initial;
    right: 50px;
    align-items: flex-end;
    width: 314px;
  }
}
.navigation__form_open{
  display:flex;
}
.navigation__form-icon{
  width: 18px;
  height: 18px;
  margin-right: 5px;
  @media(min-width:@tablet-width){
    width: 27px;
    height: 27px;
    margin-right: 11px;
  }

}
.navigation__label {
  margin-right: 13px;
  flex-grow: 1;
  @media(min-width:@tablet-width){
    margin-right: 23px;
  }
}
.navigation__input {
  padding: 0;
  border: none;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 1);
  display: block;
  width: 100%;
  outline: none;
  color: rgba(255, 255, 255, 1);
  font-family: "Open Sans",sans-serif;
  font-size: 12px;
  @media(min-width:@tablet-width){
    font-size: 14px;
    padding-bottom: 3px;
  }
}
.search-close {
  .button();
}
.search-close__icon{
  width: 15px;
  height: 15px;
  @media(min-width:@tablet-width){
    width: 29px;
    height: 29px;
  }
  @media(min-width:@desktop-width){
    width: 23px;
    height: 23px;
  }
}
.navigation__search-icon {
}
//дополнительный блок арт искусства на десктопе
.navigation-art {
  display: none;
  @media(min-width:@desktop-width){
    display: block;
    min-width: 500px;
    background-color: rgba(0, 0, 0,0.5);
    padding: 21px 50px 25px 50px;
    display: flex;
    align-items: center;
    position: absolute;
    top: 100px;
    left: 0;
    right: 0;
  }
  @media(min-width:@big-desktop-width){
    padding: 21px 159px 25px 158px;
  }
}
.navigation-art__list {
  .list();
  display: flex;
  justify-content: space-between;
  margin-right: auto;
  width: 76%;
}
.navigation-art__item {
  background-color: transparent;
  margin-top: 0;
  border: none;
  position: relative;
}
//перебивка кнопки
.navigation-art__item .navigation-art__button{
  padding: 0;
  width:initial;
  margin-left: 3px;
}
.navigation-art__item .navigation-art__button::after{
  content: '';
}
.navigation-art__item.is-active>.navigation-art__title
.navigation-art__button::after {
  content: '';
}
.navigation-art__item.is-active>.navigation-art__title
.navigation-art__button{
  transform: rotate(180deg);
  transition: transform .9s;
}
//перебивка текста
.navigation-art__item .navigation-art__panel{
  width: 300px;
  max-height: 260px;
  box-sizing: border-box;
  padding: 20px;
  overflow-y: auto;
  transition-duration: 200ms !important;
}
.navigation-art__item .navigation-art__panel
.navigation-art__text {
  font-family: "Open Sans",sans-serif;
  font-size: 14px;
  font-weight:600 ;
  color: rgba(255, 255, 255, 1);
  padding: 0;
  margin: 0;
  width: 220px;
  height: 40px;
  box-sizing: border-box;
  padding-left: 14px;
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center;
  &:not(:last-child){
    margin-bottom: 20px;
  }
}
